"use client";
import { useState, useEffect } from "react";
import axios from "axios";
import Pusher from "pusher-js";
import {
  HandThumbUpIcon,
  ChatBubbleLeftIcon,
  BellIcon,
  RssIcon,
  XMarkIcon,
} from "@heroicons/react/24/solid";
import { signIn, signOut, useSession, SessionProvider } from "next-auth/react";
import styles from "./page.module.css";

function PostsContent() {
  const { data: session, status } = useSession();
  const [posts, setPosts] = useState([]);
  const [newPost, setNewPost] = useState("");
  const [commentInputs, setCommentInputs] = useState({});
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [profile, setProfile] = useState(null);
  const [chatOpen, setChatOpen] = useState(false);
  const [chatMessages, setChatMessages] = useState([]);
  const [chatInput, setChatInput] = useState("");
  const [notificationsOpen, setNotificationsOpen] = useState(false);
  const [notifications, setNotifications] = useState([]);

  useEffect(() => {
    fetchPosts();
    if (session) {
      fetchProfile();
      setupPusher();
      setNotifications([
        { id: 1, text: "Someone liked your post!", time: new Date() },
        { id: 2, text: "New comment on your post.", time: new Date() },
      ]);
    }
    return () => {
      if (window.pusher) {
        window.pusher.disconnect();
      }
    };
  }, [session]);

  const setupPusher = () => {
    if (!session) return;

    const pusher = new Pusher("a9e989dcc04e514c8a20", {
      cluster: "ap2",
      authEndpoint: "/api/pusher/auth",
      auth: {
        headers: {
          Authorization: `Bearer ${session.accessToken || "dummy-token"}`, // Adjust based on your NextAuth setup
        },
      },
    });

    window.pusher = pusher; // Store globally to avoid multiple instances
    const channel = pusher.subscribe(`chat-${session.user.id}`);
    channel.bind("message", (data) => {
      setChatMessages((prev) => [...prev, data]);
    });
  };

  const fetchPosts = async () => {
    try {
      const res = await axios.get("/api/posts");
      console.log("Fetched posts:", res.data);
      setPosts(res.data || []);
    } catch (error) {
      console.error("Fetch posts error:", error);
      setPosts([]);
    }
  };

  const fetchProfile = async () => {
    try {
      const res = await axios.get("/api/profile");
      console.log("Fetched profile:", res.data);
      setProfile(res.data);
    } catch (error) {
      console.error("Fetch profile error:", error.response?.data || error.message);
    }
  };

  const handleManualSignup = async (e) => {
    e.preventDefault();
    try {
      await axios.post("/api/signup", { username, password });
      setUsername("");
      setPassword("");
      await signIn("credentials", {
        username,
        password,
        callbackUrl: "/posts",
        redirect: false,
      });
    } catch (error) {
      console.error("Signup error:", error.response?.data || error.message);
      alert(error.response?.data?.error || "Signup failed. Please try again.");
    }
  };

  const handlePostSubmit = async (e) => {
    e.preventDefault();
    if (!session || !newPost.trim()) return;
    const currentUsername =
      session.user?.username ||
      session.user?.name?.replace(/\s+/g, "").toLowerCase() ||
      "user";
    try {
      const res = await axios.post("/api/posts", {
        text: newPost,
        username: currentUsername,
      });
      setPosts([res.data, ...posts]);
      setNewPost("");
    } catch (error) {
      console.error("Post error:", error);
    }
  };

  const handleLike = async (id) => {
    if (!session) return alert("Please sign in first!");
    const currentUsername =
      session.user?.username ||
      session.user?.name?.replace(/\s+/g, "").toLowerCase() ||
      "user";
    try {
      const res = await axios.post(`/api/posts/${id}/like`, {
        username: currentUsername,
      });
      setPosts(posts.map((p) => (p._id === id ? res.data : p)));
      setNotifications([
        ...notifications,
        {
          id: Date.now(),
          text: `${currentUsername} liked a post!`,
          time: new Date(),
        },
      ]);
    } catch (error) {
      console.error("Like error:", error.response?.data || error.message);
      alert(error.response?.data?.error || "Failed to like post");
    }
  };

  const handleCommentSubmit = async (id, e) => {
    e.preventDefault();
    if (!session || !commentInputs[id]?.trim()) return;
    const currentUsername =
      session.user?.username ||
      session.user?.name?.replace(/\s+/g, "").toLowerCase() ||
      "user";
    try {
      const res = await axios.post(`/api/posts/${id}/comment`, {
        text: commentInputs[id],
        username: currentUsername,
      });
      setPosts(posts.map((p) => (p._id === id ? res.data : p)));
      setCommentInputs((prev) => ({ ...prev, [id]: "" }));
      setNotifications([
        ...notifications,
        {
          id: Date.now(),
          text: `${currentUsername} commented on a post!`,
          time: new Date(),
        },
      ]);
    } catch (error) {
      console.error("Comment error:", error);
    }
  };

  const handleChatSubmit = async (e) => {
    e.preventDefault();
    if (!chatInput.trim() || !session) return;
    const currentUsername =
      session.user?.username ||
      session.user?.name?.replace(/\s+/g, "").toLowerCase() ||
      "user";
    const message = {
      id: Date.now(),
      text: chatInput,
      sender: currentUsername,
      time: new Date(),
    };

    try {
      await axios.post("/api/pusher/send", {
        channel: `chat-${session.user.id}`,
        event: "message",
        data: message,
      });
      setChatMessages((prev) => [...prev, message]);
      setChatInput("");
    } catch (error) {
      console.error("Chat send error:", error);
    }
  };

  const formatDate = (date) => {
    const now = new Date();
    const diff = (now - new Date(date)) / 1000;
    if (diff < 60) return `${Math.floor(diff)} secs ago`;
    if (diff < 3600) return `${Math.floor(diff / 60)} mins ago`;
    if (diff < 86400) return `${Math.floor(diff / 3600)} hrs ago`;
    return new Date(date).toLocaleString("en-US", {
      month: "short",
      day: "numeric",
      hour: "numeric",
      minute: "2-digit",
      hour12: true,
    });
  };

  const getInitial = (name) => (name && name[0] ? name[0].toUpperCase() : "U");

  if (status === "loading") return <div className={styles.loading}>Loading...</div>;

  if (!session) {
    return (
      <div className={styles.signupContainer}>
        <div className={styles.signupCard}>
          <h2 className={styles.signupTitle}>Cousin Chronicles</h2>
          <form onSubmit={handleManualSignup}>
            <input
              type="text"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              placeholder="Username"
              className={styles.signupInput}
            />
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Password"
              className={styles.signupInput}
            />
            <button type="submit" className={styles.signupButton}>
              Sign Up
            </button>
          </form>
          <div className={styles.divider} />
          <button
            onClick={() => signIn("google", { callbackUrl: "/posts" })}
            className={styles.googleButton}
          >
            <svg width="20" height="20" viewBox="0 0 48 48">
              <path
                fill="#EA4335"
                d="M24 9.5c3.5 0 6.6 1.2 9 3.6l6.7-6.7C35.5 2.5 30 0 24 0 14.6 0 6.7 5.8 3.1 14.1l8.5 6.6c1-3.5 4-6.2 8.4-6.2z"
              />
              <path
                fill="#34A853"
                d="M24 44c6 0 11.1-2 14.8-5.5l-7.6-6c-2.1 1.4-4.8 2.2-7.2 2.2-4.4 0-8.2-2.8-9.6-6.7l-8.5 6.6C9.8 40.2 16.6 44 24 44z"
              />
              <path
                fill="#4A90E2"
                d="M44.5 24c0-1.4-.1-2.7-.4-4H24v8.1h11.8c-.5 2.5-2 4.7-4.2 6.1l7.6 6C42.5 37.5 44.5 31.1 44.5 24z"
              />
              <path
                fill="#FBBC05"
                d="M3.1 14.1C1.2 17.7 0 21.7 0 26s1.2 8.3 3.1 11.9l8.5-6.6c-.8-2.4-.8-5 0-7.3l-8.5-6.6z"
              />
            </svg>
            Sign Up with Google
          </button>
        </div>
      </div>
    );
  }

  const currentUsername =
    session.user?.username ||
    session.user?.name?.replace(/\s+/g, "").toLowerCase() ||
    "user";
  console.log("Current username:", currentUsername);

  return (
    <div className={styles.container}>
      <header className={styles.header}>
        <h2 className={styles.logo}>Cousin Chronicles</h2>
        <div className={styles.nav}>
          <button className={styles.navButton} title="Feed">
            <RssIcon width={24} height={24} />
          </button>
          <button
            className={styles.navButton}
            title="Chat"
            onClick={() => setChatOpen(!chatOpen)}
          >
            <ChatBubbleLeftIcon width={24} height={24} />
          </button>
          <button
            className={styles.navButton}
            title="Notifications"
            onClick={() => setNotificationsOpen(!notificationsOpen)}
          >
            <BellIcon width={24} height={24} />
            {notifications.length > 0 && (
              <span
                style={{
                  position: "absolute",
                  top: 2,
                  right: 2,
                  width: 8,
                  height: 8,
                  backgroundColor: "red",
                  borderRadius: "50%",
                }}
              />
            )}
          </button>
          <button
            onClick={() => signOut({ callbackUrl: "/posts" })}
            className={styles.logoutButton}
          >
            Log Out
          </button>
        </div>
      </header>

      <div className={styles.contentWrapper}>
        <aside className={styles.sidebar}>
          <div className={styles.sidebarItem}>Home</div>
          <div className={styles.sidebarItem}>Friends</div>
          <div className={styles.sidebarItem}>Groups</div>
          <div className={styles.sidebarItem}>Profile</div>
        </aside>

        <main className={styles.main}>
          {profile && (
            <div className={styles.profileCard}>
              <div className={styles.profileHeader}>
                <div className={styles.avatar}>{getInitial(currentUsername)}</div>
                <div>
                  <p className={styles.profileName}>{currentUsername}</p>
                  <p className={styles.profileDate}>
                    {formatDate(profile.createdAt)}
                  </p>
                </div>
              </div>
              <p className={styles.profileBio}>
                {profile.bio || "No bio yet"}
              </p>
            </div>
          )}

          <div className={styles.postForm}>
            <div className={styles.postInputWrapper}>
              <div className={styles.avatar}>{getInitial(currentUsername)}</div>
              <textarea
                value={newPost}
                onChange={(e) => setNewPost(e.target.value)}
                placeholder={`What's on your mind, ${currentUsername}?`}
                className={styles.postInput}
              />
            </div>
            <button
              onClick={handlePostSubmit}
              className={styles.postButton}
              disabled={!newPost.trim()}
            >
              Post
            </button>
          </div>

          {posts.length === 0 ? (
            <p className={styles.noPosts}>No posts yet.</p>
          ) : (
            posts.map((post) => (
              <div key={post._id} className={styles.postCard}>
                <div className={styles.postHeader}>
                  <div className={styles.avatar}>{getInitial(post.username)}</div>
                  <div>
                    <p className={styles.postUsername}>
                      {post.username || "Family"}
                    </p>
                    <p className={styles.postDate}>
                      {formatDate(post.createdAt)}
                    </p>
                  </div>
                </div>
                <p className={styles.postText}>{post.text}</p>
                <div className={styles.postActions}>
                  <button
                    onClick={() => handleLike(post._id)}
                    disabled={post.likedBy.includes(currentUsername)}
                    className={`${styles.actionButton} ${
                      post.likedBy.includes(currentUsername) ? styles.liked : ""
                    }`}
                  >
                    <HandThumbUpIcon width={18} height={18} />
                    {post.likedBy.includes(currentUsername)
                      ? `Liked (${post.likes})`
                      : `Like (${post.likes})`}
                  </button>
                  <button className={styles.actionButton}>
                    <ChatBubbleLeftIcon width={18} height={18} />
                    Comment ({post.comments.length})
                  </button>
                </div>
                {post.comments.length > 0 && (
                  <div className={styles.commentSection}>
                    {post.comments.map((comment, idx) => (
                      <div key={idx} className={styles.comment}>
                        <div className={styles.commentAvatar}>
                          {getInitial(comment.username)}
                        </div>
                        <div className={styles.commentBody}>
                          <p className={styles.commentUsername}>
                            {comment.username || "Family"}
                          </p>
                          <p className={styles.commentText}>{comment.text}</p>
                          <p className={styles.commentDate}>
                            {formatDate(comment.createdAt)}
                          </p>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
                <form
                  onSubmit={(e) => handleCommentSubmit(post._id, e)}
                  className={styles.commentForm}
                >
                  <div className={styles.commentAvatar}>
                    {getInitial(currentUsername)}
                  </div>
                  <input
                    type="text"
                    value={commentInputs[post._id] || ""}
                    onChange={(e) =>
                      setCommentInputs((prev) => ({
                        ...prev,
                        [post._id]: e.target.value,
                      }))
                    }
                    placeholder="Write a comment..."
                    className={styles.commentInput}
                  />
                </form>
              </div>
            ))
          )}
        </main>
      </div>

      {chatOpen && (
        <div className={styles.chatModal}>
          <div className={styles.chatHeader}>
            <span>Chat</span>
            <button
              className={styles.chatClose}
              onClick={() => setChatOpen(false)}
            >
              <XMarkIcon width={20} height={20} />
            </button>
          </div>
          <div className={styles.chatBody}>
            {chatMessages.map((msg) => (
              <div
                key={msg.id}
                className={`${styles.chatMessage} ${
                  msg.sender === currentUsername ? styles.sent : styles.received
                }`}
              >
                {msg.text}
              </div>
            ))}
          </div>
          <form onSubmit={handleChatSubmit} className={styles.chatInput}>
            <input
              type="text"
              value={chatInput}
              onChange={(e) => setChatInput(e.target.value)}
              placeholder="Type a message..."
              className={styles.chatInputField}
            />
          </form>
        </div>
      )}

      {notificationsOpen && (
        <div className={styles.notificationDropdown}>
          {notifications.length === 0 ? (
            <div className={styles.notificationItem}>No notifications</div>
          ) : (
            notifications.map((notif) => (
              <div key={notif.id} className={styles.notificationItem}>
                {notif.text} <br />
                <small>{formatDate(notif.time)}</small>
              </div>
            ))
          )}
        </div>
      )}
    </div>
  );
}

export default function PostsPage() {
  return (
    <SessionProvider>
      <PostsContent />
    </SessionProvider>
  );
}